@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#wavecontainer {
  margin: 10px;
  perspective: 1000px;
  height: 100px;
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  position:-webkit-sticky;
  position:sticky;
  top: 20px;
  z-index: 3;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  transition:1s;
  
}
#waveform {

   
    width: 80%;
    border-radius: 10px;
    box-shadow: rgb(204, 204, 204) 5px 5px 10px 0px;
    min-height: 100px;
    background-color: rgb(247, 247, 247);
    transition: 1s;
}
#waveform:hover {
    transform: scaley(1.5) scaleX(1.1) rotateX(20deg);
    box-shadow: none;
}
#wavecontainer:hover{
  top: 20px;
  
  height: 300px;
  position: absolute;
}
cursor {
  height: 100%;
  color: brown;
}
.notes {
  background-color: rgb(241, 241, 241);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1em;
}

input[type=file]::file-selector-button {
  
  border: none;
  height: 30px;
  color: white;
  background-color: black;
  cursor: pointer;
  border-radius: 5px;
}
input[type=file]::file-selector-button:hover {
  color: red;
}
body {
  padding-top: 20px;
}
.upload {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image:linear-gradient(rgb(216, 216, 216),white) ;
  transition: all .2s;
  
}
.upload:hover {
  transform: scale(1.1);
}
.upload button{
  border: none;
  background-color: rgb(248, 93, 93);
  color: rgb(255, 255, 255);
  height: 30px;
}
.upload button:hover{
  background-color: rgb(238, 34, 34);
  cursor: pointer;
}
.upload h1 {
  font-size: medium;
}
.songinfo {
    padding: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
h5 {
    margin: 3px;
    background-color: black;
    display: inline;
    color:white;
    padding: 2px;
    border-radius: 4px;
    font-family: 'EB Garamond', serif;
    transition: all .2s;
}
h5:hover{
    transform: scale(1.1);
    color: black;
    background-color: white;
}
h3 {
    display: inline;
}
.controlbutton,button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}
.controlbutton button , input {
    margin: 10px;
    
}
.controlbutton button {
    background-color: rgb(241, 241, 241);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border:0;
    transition: all .5s;
}
.controlbutton button:hover {
background-color: red;
transform: scale(1.1);
cursor: pointer;
}
.controlbutton {
    background-color: rgb(245, 245, 245);
    
}
* {
    font-family: 'Arvo', serif;
}
.header {
    background-color: rgb(255, 27, 27);
    position: fixed;
    width: 100%;
    margin-bottom: 5px;
    top: 0;
    z-index: 3;
    display: flex;
    align-content: center;
    justify-content: center;
    height: 25px;
    
}
h3 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 5px;
    color: rgb(255, 255, 255);
    font-style: italic;
    font-size: 1em;
    align-self: flex-end;
}
* {
    font-family: 'Arvo', serif;
}
span {
    text-align: center;
    

}
h2 {
    background-color: black;
    color: white;
    padding: .5em;
    border-radius: .5em;
}
img {
    margin-right: 2px;
}
.notessection {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(253, 244, 244);
}
.noteslist {
    display: grid;
    align-items: center;
    justify-content: center;
   grid-gap:.5rem 1em;
   gap:.5rem 1em;
   margin: 1em;
   transition: all .5s;
   border-radius: 10px;
   padding: 10px;
}
.noteslist:hover {
    transform: scale(1.1);
}
.notestime{

color: crimson;
display: flex;
}

.noteslable{
    grid-column-start:2 ;
    grid-column-end:4 ;
}
.notesnotes {
    grid-row-start: 2;
    grid-column-start: 1;
    grid-column-end: 5;

    background-color:rgb(53, 53, 53);
    color: white;
    border-radius: 10px;
    min-width: 500px;
    max-width: 500px;
    padding:.2em .5em ;
    font-style: italic;
    font-feature-settings: "kern";
    font-kerning: normal;

}
#delete {
background: red;
border: none;
border-radius: 1rem;
grid-row-start:1 ;
grid-column-start: 4;
width: 4em;
color: white;
place-self: start end;

}
#delete:hover{
    cursor: pointer;
}
.noteslist:nth-child(2n){
    background-color: rgb(238, 237, 237);
}
.noteslist:nth-child(2n+1){
    background-color: rgba(165, 165, 165, 0.3);
}
